import siblings from './siblings'
import isElement from './isElement'

/**
 * 获取 DOM 元素下所有 HTMKLElement 类型的 DOM 元素
 * ========================================================================
 * @method childern
 * @param {HTMLElement} el
 * @return {*[]}
 */
const children = (el) => {
  if (!isElement(el)) {
    return []
  }

  return siblings(el.firstChild, true)
}

export default children
